.checkbox input[type="checkbox"] {
  display: none;
}

.checkbox input[type="checkbox"] + label {
  width: 100%;
  padding: 10px 0;
  display: inline-block;
  font-size: 13px;
  line-height: 1.62;
  color: #3c4450;
  cursor: pointer;
  transition: all 0.3s;
}

.checkbox input[type="checkbox"] + label:hover {
  color: #36d1b2;
}

.checkbox input[type="checkbox"] + label::before {
  content: '\f00c';
  font-family: 'FontAwesome';
  color: transparent;
  position: relative;
  width: 17px;
  height: 17px;
  border-radius: 3px;
  background-color: #e1e8ee;
  margin-right: 10px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.checkbox input[type="checkbox"]:checked + label::before {
  background-color: #36d1b2;
  color: #ffffff;
}

.checkbox--type input[type="checkbox"] + label {
  width: 50%;
  padding: 0;
}

