  padding-right: 145px;
  width: 100%;
  border-radius: 4px;
  display: inline-flex;
  border-style: solid;
  border-width: 3px;
  border-image-source: linear-gradient(to bottom, #42d79f, #27c8ca);
  border-image-slice: 1;
  background: transparent;
  line-height: 1.5;
  font-size: 14px;
  color: #ffffff;
}

.stub__content form input[type="email"]::-webkit-input-placeholder {
  color: #ffffff;
}

.stub__content form input[type="email"]::-moz-placeholder {
  color: #ffffff;
}

.stub__content form input[type="email"]:-moz-placeholder {
  color: #ffffff;
}

.stub__content form input[type="email"]:-ms-input-placeholder {
  color: #ffffff;
}

.stub__content form input[type="submit"] {
  width: 145px;
  background: linear-gradient(to bottom, #42d79f, #27c8ca);
  padding: 17px 25px;
  padding-bottom: 18px;
  position: absolute;
  right: -5px;
  line-height: 1.5;
  font-size: 16px;
  color: #ffffff;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}

.stub__social {
  padding-bottom: 40px;
}

.stub__social p {
  padding-bottom: 30px;
  font-weight: 600;
}

.stub__social a {
  width: 36px;
  height: 36px;
  border-radius: 4px;
  background-image: linear-gradient(to bottom, #42d79f, #26c8cb);
  margin-right: 15px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-bottom: none;
}

.stub__social a:last-child {
  margin: 0;
}

.stub__social a i {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-bottom: none;
  color: #ffffff;
  transition: all 0.3s;
}

.stub__social a:hover i {
  color: #3c4450;
}
